import React from "react";

interface DropdownProps {
  // Add any props specific to the Dropdown component
  disabled?: boolean;
}

const Dropdown: React.FC<DropdownProps> = ({ children, disabled }) => {
  return (
    <div
      style={{
        backgroundColor: disabled ? "#dddddd" : "white",
        cursor: disabled ? "not-allowed" : "pointer",
      }}
    >
      {children}
    </div>
  );
};

export default Dropdown;
